// Front matter need for Jekyll to compile for... some reason?

@charset 'UTF-8';

@import 'utils.scss';
@import 'animations.scss';

@import 'anchor.scss';

body {
    margin: 0;
    padding: 0;
    text-align: center;
    font-family: "Josefin Sans", sans-serif;
    background-color: #f2f2f2;
}

.slogan {
    max-width: calc(100% - 40px);
    margin-top: 4px;
    max-height: 24px;
    margin-bottom: -4px;
    pointer-events: none;
}

.title {
    max-width: 750px;
    width: calc(100% - 40px);
    margin-top: 10px;
    margin-bottom: 6px;
    pointer-events: none;
}

ol {
    display: inline-block;
    width: 100%;
    margin: 14px 0 0 0;
    padding: 0 0 14px 0;
    border-bottom: 2px solid black;
}

li {
    display: inline-block;
    font-size: 18px;
    margin: 0 10px;
    text-transform: uppercase;
    font-size: 14px;
}

.line {
    height: 1px;
    width: 100%;
    background-color: black;
    margin-top: 2px;
    margin-bottom: 2px;
}

.map-box {
    max-width: 1000px;
    margin: 8px auto 6rem;
    position: relative;
    padding: 0 10px;
}

.item {
    margin: 0 0 0 0;
    padding: 0 4px;
    display: inline-block;
    vertical-align: top;

    font-family: 'Lora', sans-serif;
    text-align: left;
    font-size: 14px;
    line-height: 20px;

    a { 
        text-transform: uppercase;
        font-weight: 800;
    }

    &_half {
        width: calc(50% - 16px);
    }

    &_black img {
        border-color: black !important;
    }

    img {
        border: 1px solid #aaa;
        max-width: 100%;
        transition: 0.2s;

        &:hover {
            cursor: pointer;
            transform: translateY(-2px);
            box-shadow: 0 2px 0 black;
        }
    }

    p {
        margin-top: 2px;
        margin-bottom: 20px;
    }
}

#footer {
    border-top: 1px solid black;
    margin-top: 2px;
    padding: 12px 0;
    width: 100%;
}

.social-icon {
    width: 20px;
    height: 20px;
    vertical-align: bottom;
    transition: 0.2s;

    &:hover {
        opacity: 0.5;
    }
}

.about-cont {
    margin: 4rem auto 6rem;
    max-width: 500px;
    text-align: left;
    font-size: 18px;
    line-height: 30px;
    font-family: 'Lora', sans-serif
}

.wip {
    display: none;
    padding: 10px 0;
    background-color: rgb(255, 255, 177);
}
